// sass-lint:disable variable-name-format mixin-name-format

// Instantiate Gritacular grid
// using the normal grid settings
@include grid_generate($vl-grid_args);


// Instantiate Gritacular grid
// using the form grid settings
@include grid_generate($vl-form_grid_args);

// Extend generated grid
.vl-grid {
  &--is-stacked-large {
    margin-bottom: -#{$vl-spacing-large};

    > * {
      margin-bottom: $vl-spacing-large;
    }
  }
  &--is-stacked-small {
    margin-bottom: -#{$vl-spacing-small};

    > * {
      margin-bottom: $vl-spacing-small;
    }
  }
}
