// sass-lint:disable no-vendor-prefixes
@mixin reset-button {
  border-radius: 0;
  appearance: none;
  -webkit-appearance: none; // doesn't work without prefix on Safari iOS11
  border: 0;
  background-color: transparent;
  padding: 0;
}

@mixin button-action {
  @include reset-button;

  display: block;
  width: $vl-form-input-height;
  height: $vl-form-input-height;
  background-color: $vl-white;
  border: 0;
  border-radius: 2px;
  font-size: 16px;
  line-height: 18px;
  display: inline-flex;
  align-items: center;
  justify-content: center;

  &__icon {

  }

  &:hover {
    background: $vl-alt-bg;
    color: $vl-text-color;
  }

  &--active {
    @include button-action--active;
  }

  &--disabled {
    @include button-action--disabled;
  }
}

@mixin button-action--active {
  background-color: $vl-action-active-color;
  color: $vl-white;

  &:hover {
    background: $vl-action-active-color;
    color: $vl-white;
  }
}

@mixin button-action--disabled {
  background-color: $vl-disabled-bg-color;
  color: $vl-disabled-text-color;

  &:hover {
    background-color: $vl-disabled-bg-color;
    color: $vl-disabled-text-color;
  }
}
