.vl-icon-wrapper {
  display: flex;

  .vl-icon {
    display: inline-flex;
  }
}

.vl-icon {
  font-size: 1em;
  display: inline;
  color: inherit;

  svg {
    height: 1em;
    width: auto;
    fill: currentColor;
    display: inline-block;
    font-size: inherit;
    overflow: visible;
  }

  &--small {
    font-size: 0.8em;

    svg {
      width: 0.8em;
    }
  }

  &--large {
    font-size: 1.2em;
    
    svg {
      width: 1.2em;
    }
  }

  &--light {
    color: $vl-sub-text-color;
  }

  &--before {
    margin-right: 0.5em;
  }

  &--after {
    margin-left: 0.5em;
  }

  // if iconfont is also applied beside the inline-svg, hide iconfont to prevent doubles
  &--inline-svg {
    &:before {
      display: none;
    }
  }
}
